<template>
  <div>
    <van-form @submit="onSubmit">
    <van-nav-bar title="报修分配（宿舍）" />
        <van-field v-model="fid" name="fid" style="display:none"/>
        <van-cell title="报修人:"  :value="frepair_person_name"  />
        <van-cell title="楼号:"  :value="frepair_build"  />
        <van-cell title="房间号:" :value="room_num"  />
        <van-cell title="报修类型:" :value="frepair_type"  />
        <van-cell title="故障描述:" :value="frservice_describe" />
        <van-button type="info" plain  block @click="getimg()"  style="width:90%;margin:10px auto">查看维修图片</van-button>
        <van-field
            readonly
            clickable
            name="repair_user"
            :value="repairPerson"
            label="维修工"
            placeholder="点击选择维修工"
            @click="showPicker = true"
            />
        <van-field  name="repair_user_id" v-model="repair_user_id" label="维修工：" style="display:none" />
        <van-popup v-model="showPicker" position="bottom">
            <van-picker
                show-toolbar
                :columns="repair_person"
                @confirm="person_confirm"
                @cancel="showPicker = false"
                value-key="repair_user"
            />
        </van-popup>
        <van-field
            readonly
            clickable
            name="repair_fin_out"
            :value="repair_fin_out"
            label="维修类型"
            placeholder="点击选择维修类型"
            @click="show= true"
        />
        <van-popup v-model="show" position="bottom">
            <van-picker
                show-toolbar
                :columns="repair_finout"
                @confirm="finout_confirm"
                @cancel="show = false"
            />
        </van-popup>
         <van-field
            name="message"
            v-model="remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="100"
            placeholder="请输入"
            show-word-limit
        />
        <van-button  block type="info"  native-type="submit"  style="width:100%;position: fixed;bottom:0px;left:0px;">提交</van-button> 
    </van-form>
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
import {apidorminfo} from "@/api/index"
import {apigetrepair_person} from "@/api/index"
import {apidistributesave} from "@/api/index"
import { Toast } from 'vant';
export default {
    name:'dormDistribute',
    data(){
        return{
            fid:"",//待维修列表id
            frepair_type:"",//待维修列表渲染类型
            frepair_build:"",//待维修列表楼号
            frepair_person_name:"",//待维修报修人
            room_num:"",//待维修列表房间
            frepair_date:"",//待维修报修时间
            area:"",//待维修列表渲染区域
            frservice_describe:"",//待维修列表渲染故障描述
            img:[],//待维修列表渲染图片
            remark:"",
            repair_person:[],
            showPicker:false,
            repairPerson:"",
            repair_user_id:'',
            show:false,
            repair_fin_out:'',
            repair_finout:['内修','外修','驳回']
        
        }
    },
    methods:{
        // 图片预览
        getimg(){
            ImagePreview(this.img);
        },
        // 待维修信息渲染
        getrepairinfo(){
            var _this=this
            let params = {fid: this.fid}
            apidorminfo(params).then(res=>{
                console.log(res.data.data)
                var data=res.data.data[0];
                    _this.frepair_type=data.room_repair_type;
                    _this.frepair_build=data.floor_num;
                    _this.frservice_describe=data.room_repair_describe;
                    _this.frepair_person_name=data.person_name;
                    _this.room_num=data.room_num;
                for(var i=0;i<data.file_path.length;i++){
                    _this.img.push(data.file_path[i].url)
                }
            })
        },
        getrepair_person(){
            var _this=this
             let params = {fid: this.fid}
            apigetrepair_person(params).then(res=>{
                console.log(res.data)
                _this.repair_person=res.data.data
            })
        },
        person_confirm(value) {
            this.repairPerson = value.repair_user;
            this.repair_user_id=value.fid;
            this.showPicker = false;
        },
        finout_confirm(value) {
            this.repair_fin_out=value
            this.show = false;
        },
        
        // 提交
        onSubmit(values) {
            this.$loading('提交中');
            debugger
            apidistributesave(values).then(res=>{
                if(res.data.code==200){
                    this.$loading.close();
                    Toast.success(res.data.msg);
                    setTimeout(() =>{
                        this.$router.push({
                            path:"/distributelist"
                        });
                    },3000);
                }else{
                    this.$loading.close();
                    Toast.success(res.data.msg);
                }     
            })
        }
    },
    created(){
        this.fid=this.$route.query.fid
        this.getrepairinfo();
        this.getrepair_person();
    }
}
</script>

<style>
.van-cell__value{
    flex: 2;
}
body{
    height: 100%;
}

</style>


